import { Component } from '@angular/core';

import { ApiClientService } from "./services/api-client.service";
import {Place} from "./data-model/place";

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'app';
  heading; 

  searchable     = [];
  viewable;
  seniorAccounts = [];
  taxFree        = []; 
  highInterest   = [];
  youthAccounts  = [];

/* 
  selected= { 
    selectedHighInterest : true,
    selectedTaxFree : false,
    selectedSeniorAccounts : false,
    selectedYouthAccounts : false
  }
*/


  constructor(
    private apiClient : ApiClientService
  ){ 
    
    apiClient.getHighInterest().subscribe( data => { 
      
      data.map( place => {
        let currMap = new Place(
            place.brokerName, 
            place.brokerLogo, 
            place.location, 
            place.interestRate, 
            place.returnAmount,
            place.type, 
            place.createdData
          )

        this.highInterest.push( currMap );  
        this.searchable.push( currMap );
      });

    });

    apiClient.getSeniorAccounts().subscribe( data => { 
      
      data.map( place => { 
        let currMap = new Place(
            place.brokerName, 
            place.brokerLogo, 
            place.location, 
            place.interestRate, 
            place.returnAmount,
            place.type, 
            place.createdData
          )

        this.seniorAccounts.push( currMap );
        this.searchable.push( currMap );  
      });

    } );

    apiClient.getTaxFree().subscribe( data => { 
      data.map( place => { 
        let currMap = new Place(
            place.brokerName, 
            place.brokerLogo, 
            place.location, 
            place.interestRate, 
            place.returnAmount,
            place.type, 
            place.createdData
          )

        this.taxFree.push( currMap );
        this.searchable.push( currMap );  
      });
    } );

    apiClient.getYouthAccounts().subscribe( data => { 
      data.map( place => { 
        let currMap =new Place(
            place.brokerName, 
            place.brokerLogo, 
            place.location, 
            place.interestRate, 
            place.returnAmount,
            place.type, 
            place.createdData
          )

        this.youthAccounts.push( currMap );  
        this.searchable.push( currMap );  
      });
    } );

    this.viewable = this.seniorAccounts;
    this.heading = "Senior Accounts";
  }

  seniorClick(){ 
    this.viewable = this.seniorAccounts;
    this.heading = "Senior Accounts";
  };
  taxFreeClick(){ 
    this.viewable = this.taxFree;
    this.heading = "Tax Free Accounts";
  }
  highInterstClick(){
    this.viewable = this.highInterest; 
    this.heading = "High Interest Accounts";
  }
  youthClick(){ 
    this.viewable = this.youthAccounts;
    this.heading = "Youth Accounts";
  }


/*
  tabClicked( selectedTab ){ 
    let props = Object.keys( this.selected );
    for( var x = 0; x < props.length; x++ ){ 
      if( x == selectedTab ){ 
        this.selected[props[x]] = true;
      }
      else{
        this.selected[props[x]] = false;
      }
    }
  }
*/


}



